import React, { Component } from "react";
import Table from "./Table";
import Form from "./Form";
import TopBar from "./TopBar";
import CachorroSebastiao from './assets/images/3_cachorroAsebastiao.jpg';
import Hamburguer from './assets/images/4_burguer.jpg';
import Francesinha from './assets/images/5_francesa.jpg';

class App extends Component {
	state = {
		characters: []
	};

	render() {
		return (
			<div>
        <TopBar />
        <div>
        {<img src={CachorroSebastiao}  height='100%' width='100%' object-fit='contain' alt=''></img>}
        {<img src={Hamburguer}  height='100%' width='100%' object-fit='contain' alt=''></img>}
        {<img src={Francesinha}  height='100%' width='100%' object-fit='contain' alt=''></img>}
        </div>
				<div className="container">
					<Table
						characterData={this.state.characters}
						removeCharacter={this.removeCharacter}
					/>
					<Form handleSubmit={this.handleSubmit} />
				</div>
			</div>
		);
	}

	removeCharacter = index => {
		const { characters } = this.state;

		this.setState({
			characters: characters.filter((character, i) => {
				return i !== index;
			})
		});
	};

	handleSubmit = character => {
		this.setState({ characters: [...this.state.characters, character] });
	};
}

export default App;
